___CSS_LOADER_EXPORT___.push([module.id, `*, ::after, ::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html{
  height: 100vh;
  background-image: radial-gradient(circle at center center, transparent 0%,rgb(241,246,249) 85%),linear-gradient(78deg, rgba(192, 192, 192,0.05) 0%, rgba(192, 192, 192,0.05) 50%,rgba(60, 60, 60,0.05) 50%, rgba(60, 60, 60,0.05) 100%),linear-gradient(227deg, rgba(97, 97, 97,0.05) 0%, rgba(97, 97, 97,0.05) 50%,rgba(52, 52, 52,0.05) 50%, rgba(52, 52, 52,0.05) 100%),linear-gradient(240deg, rgba(98, 98, 98,0.05) 0%, rgba(98, 98, 98,0.05) 50%,rgba(249, 249, 249,0.05) 50%, rgba(249, 249, 249,0.05) 100%),linear-gradient(187deg, rgba(1, 1, 1,0.05) 0%, rgba(1, 1, 1,0.05) 50%,rgba(202, 202, 202,0.05) 50%, rgba(202, 202, 202,0.05) 100%),linear-gradient(101deg, rgba(61, 61, 61,0.05) 0%, rgba(61, 61, 61,0.05) 50%,rgba(254, 254, 254,0.05) 50%, rgba(254, 254, 254,0.05) 100%),linear-gradient(176deg, rgba(237, 237, 237,0.05) 0%, rgba(237, 237, 237,0.05) 50%,rgba(147, 147, 147,0.05) 50%, rgba(147, 147, 147,0.05) 100%),linear-gradient(304deg, rgba(183, 183, 183,0.05) 0%, rgba(183, 183, 183,0.05) 50%,rgba(57, 57, 57,0.05) 50%, rgba(57, 57, 57,0.05) 100%),radial-gradient(circle at center center, rgb(57,72,103),rgb(57,72,103));
}

header{
  background-color: #212A3E;
  display: flex;
  color: #F1F6F9;
  justify-content: center;
  align-items: center;
  height: 5em;
}

header h1{
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: 46px;
  color: #F1F6F9;
 
}

.search-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 50px;
  margin-bottom: 10px;
}

.search-input {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  width: 200px;
}

.section-card-container{
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(600px, 0fr));
  justify-content: center;
  grid-row-gap: 30px;
}

.card{
  border: 2px solid #F1F6F9;
  width: 20em;
  border-radius: 5px;
  margin: 0 auto;
  background-color: #F1F6F9;
}

section div .card-img{
  width: 100%;
  height: 250px;
}

section .card-title{
  font-size: 24px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  text-align: center;
  margin-top: 10px;
}

section .card-text{
  font-size: 16px;
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: center;
  padding: 10px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

section .card-btn{
  margin-top: 30px;
  display: block;
  margin: 10px auto;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  width: 40%;
  padding: 10px;
  border-radius: 3px;
  border: 0px;
  cursor: pointer;
  background-color: #212A3E;
  color: #F1F6F9;
}

section .card-btn:hover{
  cursor: pointer;
  color: #F1F6F9;
  background-color: #394867;
}







.card-container-water{
border: 1px solid blue;
background-color: rgba(16, 181, 246, 0.492);
border-radius: 10px;
width: 300px;
margin-top: 10px;
padding: 5px;
}

.card-container-fire{
  border: 1px solid red;
  border-radius: 10px;
  background-color: rgba(255, 0, 0, 0.369);
  width: 300px;
  margin-top: 10px;
  padding: 5px;
}

